import '../App.css';
import './fani.css';
import React, { useState } from 'react';
import selaus from './Selaus';
function Selaa({ selaa }) {
    return (
      <div className="selattavat" >
       
            <h3> {selaa.name}</h3>
            <p>Hinta: {selaa.hinta} €</p> 
            
       <a>Ostoskorissa: {selaa.amount}</a><br></br>
      
  
      </div>
   
    )
  }
  const Yhteenveto =({selaus,tiedot,setTiedot}) => {
    
    function calTotal(){
  
      const amounts = selaus.map(selaa => selaa.amount * selaa.hinta);
      const total = amounts.reduce((a, b) => a + b);
      return total 
      }
 
   const filtered = selaus.filter(selaa => selaa.amount > 0)
 
   
      return (
        
          <div className ="selattavat">
         <h2>Asiakkaan tiedot</h2>
        {tiedot.name} <br></br>
        {tiedot.email} <br></br>
        {tiedot.puhelin} <br></br>
        {tiedot.Koite} <br></br>
        {tiedot.Posite} <br></br>
        {tiedot.PTP} <br></br>
          {filtered.map(selaa=><Selaa key ={selaa.id} tiedot={tiedot}  selaa={selaa} />)}
          <p>Yhteensä : {calTotal() } €</p>
          </div>
      )
  }
export default Yhteenveto;