import React, { useState } from 'react';
import './App.css';
import Selaus from './components/Selaus';
import selaus from './components/selaus.tuotteet';
import Tilaukset from './components/Tilaukset';
import Ostoskori from './components/Ostoskori';
import Yhteenveto from './components/Yhteenveto';
function App() {
  const [tuotteet, setTuotteet]= useState (selaus)
  const [page, setPage] = useState (1)
  const [tiedot, setTiedot]= useState ({id: 0,name: "", email:"", puhelin:"",Kosoite:"",Posoite:"", PTP:"" })
  function zero() {
    const tempLink = selaus.map(selaa => {
    
      if (selaa.amount > 0) {
        selaa = { ...selaa, amount: (selaa.amount = 0) };
   
   }
     return selaa;
    })
    setTuotteet(tempLink)
  }
 
  return (
    <div className="App">
           <header>
             
             <h2>Fanikauppa</h2>
               <div class="vertical-menu">
                 <button type="button" onClick={e => zero(selaus.id)} >Nollaa </button>
               </div>
           </header> 
     <br></br>
     {page === 1 && <button onClick={()=>setPage(2)}>ostoskori</button>
    } 
     {page === 1 && 
     <Selaus selaus={tuotteet} setTuotteet={setTuotteet}/> } <br></br>
      {page === 3 && <button onClick={()=>setPage(1)}>takaisin tuotteisiin</button>}
  {page === 3  && 
     <Tilaukset tiedot={tiedot} setTiedot={setTiedot} page={page} setPage={setPage}/>
     }
   {page === 4 && 
     <Yhteenveto selaus={tuotteet} setTuotteet={setTuotteet} tiedot={tiedot} setTiedot={setTiedot}/> } 
       {page === 4 && <button    onClick={() => {
          zero();
          setPage(1);
        }}>hyväksy ostokset ja palaa takaisin tuotteisiin</button>}
     {page === 2 && <button onClick={()=>setPage(1)}>palaa takaisin tuotteisiin</button>} <br></br>
     {page === 2 &&  <Ostoskori setPage={setPage} selaus={tuotteet} setTuotteet={setTuotteet} /> } <br></br>
       {page === 2 && <button onClick={()=>setPage(3)}>hyvaksy</button>
    }
     
   
    </div>
  );
}

export default App;
