import '../App.css';
import './fani.css';
import React from 'react';
function Selaa({ selaa ,addamount,mamount}) {
    return (
      <div className="selattavat" >
       
            <h3> {selaa.name}</h3>
            <img src={selaa.url} alt="kuva" />
            <p>Hinta: {selaa.hinta} €</p> 
            
       <a>Ostoskorissa: {selaa.amount}</a><br></br>
        <button type="button" onClick={e => addamount(selaa.id)} >lisaa:{selaa.amount} </button>
        <button type="button" onClick={e => mamount(selaa.id)} >vähennä:{selaa.amount} </button>
        
      </div>
   
    )
  }

  const Selaus =({selaus, setTuotteet}) => {
   
  function addamount(id) {
    const tempLink = selaus.map(selaa => {
      if (selaa.id === id) {
        selaa = { ...selaa, amount: (selaa.amount + 1) };
      }
     return selaa;
    })
    setTuotteet(tempLink)
  }

  function mamount(id) {
    const tempLink = selaus.map(selaa => {
    
      if ((selaa.id === id) && (selaa.amount > 0)) {
        selaa = { ...selaa, amount: (selaa.amount - 1) };
   
   }
     return selaa;
    })
    setTuotteet(tempLink)
  }
    return (
      
        <div className ="selattavat">
        {selaus.map(selaa=><Selaa key ={selaa.id}  selaa={selaa} addamount={addamount} mamount={mamount}  />)}
        </div>
    )
}
export default Selaus;