import '../App.css';
import './fani.css';
import React, { useState } from 'react';

function Selaa({ selaa ,addamount,mamount}) {
    return (
      <div className="selattavat" >
       
            <h3> {selaa.name}</h3>
            <p>Hinta: {selaa.hinta} €</p> 
            
       <a>Ostoskorissa: {selaa.amount}</a><br></br>
        <button type="button" onClick={e => addamount(selaa.id)} >lisaa:{selaa.amount} </button>
        <button type="button" onClick={e => mamount(selaa.id)} >vähennä:{selaa.amount} </button>

      </div>
   
    )
  }
  const Ostoskori =({selaus, setTuotteet}) => {
   
    function addamount(id) {
      const tempLink = selaus.map(selaa => {
        if (selaa.id === id) {
          selaa = { ...selaa, amount: (selaa.amount + 1) };
        }
       return selaa;
      })
      setTuotteet(tempLink)
    }
 
     
    function mamount(id) {
      const tempLink = selaus.map(selaa => {
      
        if ((selaa.id === id) && (selaa.amount > 0)) {
          selaa = { ...selaa, amount: (selaa.amount - 1) };
     
     }
       return selaa;
      })
      setTuotteet(tempLink)
    }
    
   
  function calTotal(){
  
  const amounts = selaus.map(selaa => selaa.amount * selaa.hinta);
  const total = amounts.reduce((a, b) => a + b);
  return total 
  }
   const filtered = selaus.filter(selaa => selaa.amount > 0)

      return (
        
          <div className ="selattavat">
          {filtered.map(selaa=><Selaa key ={selaa.id}  selaa={selaa} addamount={addamount} mamount={mamount} />)}
      <p>Yhteensä : {calTotal()} €</p>
          </div>
      )
  }
  export default Ostoskori;