import '../App.css';
import './fani.css';
import React, { useState } from 'react';
import selaus from './Selaus';

function Kori({kori}){
  return(
    <div className="ostoskori">
    
    </div>
  )
    }
  const Tilaukset =({tiedot,setTiedot,page,setPage}) => {

    
    const submitHandler = (e) => {
      e.preventDefault();
   setPage(4)
  
    }
    const submitHyndler = (e) => {
      e.preventDefault();
      const tempLink = { id: tiedot.length + 1,name: "", email: "", puhelin: "", Kosoite: "", Posoite: "", PTP: "" }
      setTiedot(tiedot.concat([tempLink]));
      setTiedot("");
      
  console.log(setTiedot);
    }  
    
    const infoChangeHandler = (event, field) => {
      const tempInfo = {...tiedot};
      tempInfo[field] = event.target.value;
      setTiedot(tempInfo);
  };

    return(
    <div className="ostoskori"> 
    <form onSubmit={(e)=>submitHandler(e)}>
      <p>Nimi</p>
      <input type="text" name="name" value={tiedot.name} onChange={e => infoChangeHandler(e,'name')} required></input>
      <p>Sähköposti</p>
      <input type="email" name="sahkoposti" value={tiedot.email} onChange={e => infoChangeHandler(e,'email')}></input>
      <p>Puhelin numero</p>
      <input type="text" name="puhelinnumero" value={tiedot.puhelin} onChange={e => infoChangeHandler(e,'puhelin')} required></input>
      <p>Katuosoite</p>
      <input type="text" name="katuosoite" value={tiedot.Kosoite} onChange={e => infoChangeHandler(e,'Kosoite')} required></input>
      <p>postinumero</p>
      <input type="text" name="postiosoite" value={tiedot.Posite}  onChange={e => infoChangeHandler(e,'Posite')} required></input>
      <p>postitoimipaikka</p>
      <input type="text" name="postitoimipaikka" value={tiedot.PTP} onChange={e => infoChangeHandler(e,'PTP')} required></input><br></br><br></br>
      <button type="submit" >Palauta</button>
     </form>
     
    </div> 
     )
    
  }
export default Tilaukset;
